.main__header{
    width: 100vw;
    height: clac(100vh -rem);
    display: grid;
    place-items:center;
    margin-top: 3rem;
}

.main__header-container{
    height: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items:center;
    justify-content: space-between;
    justify-content: space-between;
    gap: 6rem;
}

.main__header-left{
    margin-top: -3rem;
}
.main__header-left h4{
    margin-bottom:1rem;
    color: var(--color secondary);
    font-weight: 400;
}

.main__header-left p{
    margin: 1rem 0 3.5rem;
    font-size: 1.1rem;
}

.main__header-right{
    display: grid;
    place-items: center;
    position: relative;
}

.main__header-circle{
    width: 25rem;
    height: 25rem;
    background: linear-gradient(100deg, #c297a1,transparent);
    border-radius: 50%;
    position: absolute;
    /*filter: blur(2px);*/
    transition: var(--transition);
}

.main__header-image{
    position: relative;

}

.main__header-right:hover .main__header-circle{
    filter: blur(10px);
}

/*Programs*/
.programs {
    margin-top: 4rem;
}

.programs__wrapper {
    display:grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 3rem;
    margin-top: 4rem;
}


.programs__program a{
    margin-top: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
}


.programs__program:hover a{
    background: black;
    color: #c297a1;
}


/*values*/
.values__container{
    display: grid;
    grid-template-columns: 38% 50%;
    gap: 12%
}

.values__image{
    filter: saturate(0.25);
    transition: var(--transition);
}
.values__image:hover{
    filter: saturate(1);
}

.values__right > p{
    margin: 1.5rem 0 5rem;
}

.values__wrapper{
    display:grid;
    grid-template-columns: 1fr 1fr;
    gap: 3.4rem 2.4rem;
}

.card.values__value{
    padding-top: 3.5rem;
    text-align:left;
    position: relative;
}

.card.values__value span{
    position: absolute;
    top: -1.5rem;
}